////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// layout mixins - start

@mixin edgtfRelativeHolderLayout($vertical-align: middle) {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin edgtfAbsoluteHolderLayout() {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@mixin edgtfTableLayout() {
    position: relative;
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
}

@mixin edgtfTableCellLayout($vertical-align: middle) {
    position: relative;
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin edgtfTypographyLayout($important: null) {
    color: inherit $important;
    font-family: inherit $important;
    font-size: inherit $important;
    font-weight: inherit $important;
    font-style: inherit $important;
    line-height: inherit $important;
    letter-spacing: inherit $important;
    text-transform: inherit $important;
}

@mixin edgtfBody() {
    font-family: $default-text-font;
    font-size: 17px;
    font-weight: 400;
    line-height: 29px;
    color: $default-text-color;
    background-color: $default-background-color;
    overflow-x: hidden !important;
    -webkit-font-smoothing: antialiased;
}


// layout mixins - end

// checkbox mixins - start

$checkbox-size: 15px;
$checkbox-border-width: 1px;

%checkbox-style {
    position: relative;
    margin: 8px 0;
    line-height: 1;

    input[type=checkbox] {
        width: $checkbox-size;
        height: $checkbox-size;
        max-height: $checkbox-size;
        position: relative;
        display: inline-block;
        vertical-align: top;
        top: 0;
        left: 0;
        margin: 0;
    }

    input[type=checkbox] + label {
        position: absolute;
        top: 0;
        left: 0;
        display: inline-block;
        line-height: 0;
        pointer-events: none;
        cursor: pointer;
    }

    input[type=checkbox] + label span.edgtf-label-text {
        display: inline-block;
        padding-left: 10px;
        line-height: $checkbox-size;
        color: $default-heading-color;
    }

    input[type=checkbox] + label .edgtf-label-view {
        display: inline-block;
        vertical-align: top;
        width: $checkbox-size;
        height: $checkbox-size;
        background-color: $default-background-color;
        border: $checkbox-border-width solid $default-border-color;
        border-radius: 2px;
        cursor: pointer;
        box-sizing: border-box;

        &:hover {
            cursor: pointer;
        }
    }

    input[type=checkbox] + label .edgtf-label-view:after {
        content: '';
        position: absolute;
        top: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        left: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        width: $checkbox-size / 2 - $checkbox-border-width;
        height: $checkbox-size / 2 - $checkbox-border-width;
        background-color: $first-main-color;
        opacity: 0;
        transition: opacity 0.3s ease-in-out;
    }

    input[type=checkbox]:checked + label .edgtf-label-view:after {
        opacity: 1;
    }
}

// checkbox mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// common mixins - start

@mixin edgtfBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin edgtfImageOverlayHoverStyle($with-hover: true) {
    
    @if ($with-hover) {
        
        &:hover {
            
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include edgtfAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-heading-color, .4);
            opacity: 0;
            transition: opacity .2s ease-in-out;
        }

    } @else {
        @include edgtfAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;
        transition: opacity .2s ease-in-out;
    }
}

@mixin edgtfStandardPaginationStyle($list_type: null) {
    @include edgtfRelativeHolderLayout(top);
    margin: 40px 0 0;
    clear: both;
    text-align: center;

    ul {
        @include edgtfRelativeHolderLayout(top);
        padding: 0;
        margin: 0;
        list-style: none;

        li {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin: 0 3px;
            transition: background-color 0.3s ease, color 0.3s ease;

            &.edgtf-pag-number {

                &.edgtf-pag-active,
                &:hover {
                    background-color: #000;

                    a {
                        color: #fff;
                    }
                }

            }

            a,
            span {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0;
                padding: 0 15px 0 17px;
                @include edgtfH6Aux();
                line-height: 41px;
            }

            &.edgtf-pag-active {
                
                a {
                    color: #fff;
                }
            }

            &.edgtf-pag-prev{
                float: left;    
            }
            
            &.edgtf-pag-next{
                float: right;
            }
            
            &.edgtf-pag-prev,
            &.edgtf-pag-next,
            &.edgtf-pag-first,
            &.edgtf-pag-last {
                margin: 0 2px;

                a {

                    span {
                        display: block;
                        line-height: inherit;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                }
            }

            @if ($list_type == 'shortcode') {
                
                &.edgtf-pag-prev {
                    
                    a {
                        opacity: 0;
                    }
                }

                &.edgtf-pag-next {
                    
                    a {
                        opacity: 1;
                    }
                }

            }
        }
    }

    @if ($list_type == 'shop') {
        a,
        span {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 3px;
            padding: 0 15px 0 17px;
            @include edgtfH6Aux();
            line-height: 41px;
        }
        
        span{
            background-color: #000;
            color: #fff;
        }
        
        a{
            
            &.prev{
                float: left;
                margin: 0;
                padding: 0;
            }
            
            &.next{
                float: right;
                margin: 0;
                padding: 0;
            }

            &:not(.prev):not(.next) {
                &:hover {
                    background-color: #000;
                    color: #fff;
                }
            }
        }
    }

}

@mixin edgtfBlockquote($important : null) {
    @include edgtfRelativeHolderLayout();
    margin: 10px 0 $important;
    padding: 20px 40px $important;
    font-size: 18px $important;
    line-height: 30px $important;
    text-align: left;
    quotes: none;
    box-sizing: border-box;
    border: none $important;
    color: $default-text-color $important;
    font-weight: 600;

    > * {
        @include edgtfTypographyLayout();
        margin: 0;
    }

    &:before,
    &:after {
        content: '';
    }

    cite,
    .wp-block-quote__citation,
    .wp-block-pullquote__citation,
    footer {
        display: block $important;
        margin-top: 10px $important;
        text-align: inherit $important;
        font-size: 14px $important;
        line-height: 1.3em $important;
        letter-spacing: 0 $important;
        font-style: normal  $important;
        font-weight: 400 $important;
        text-transform: none $important;
        color: #000;
    }

}

@mixin edgtfButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: $additional-heading-font;
    font-size: 15px;
    line-height: 2em;
    letter-spacing: 0.1em;
    font-weight: 700;
    text-transform: uppercase;
    outline: none;
    box-sizing: border-box;
    transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
}

@mixin edgtfButtonSize($size: medium) {
    
    @if ($size == 'small') {
        padding: 11px 36px;

    } @else if ($size == 'medium') {
        padding: 16px 68px;

    } @else if ($size == 'large') {
        padding: 13px 66px;

    } @else if ($size == 'huge') {
        display: block;
        width: 100%;
        text-align: center;
        padding: 13px 26px;
    }
}

@mixin edgtfButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin edgtfButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $default-heading-color $important;
    border: 1px solid transparent $important;
}

@mixin edgtfButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
}

@mixin edgtfButtonOutlineColor($important: null) {
    color: $first-main-color $important;
    background-color: transparent $important;
    border: 1px solid $first-main-color $important;
}

@mixin edgtfButtonOutlineHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border-color: $first-main-color $important;
}

@mixin edgtfPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }
    &::-moz-placeholder {
        @content;

        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

// common mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// mixins styles - start

%input-style {
    position: relative;
    width: 100%;
    margin: $input-margin;
    padding: $input-vertical-padding $input-horizontal-padding;
    font-family: inherit;
    font-size: 15px;
    font-weight: inherit;
    line-height: calc(#{$input-height} - (#{$input-vertical-padding} * 2) - 2px);
    color: $default-heading-color;
    background-color: #fff;
    border: 1px solid $default-border-color;
    border-radius: 0;
    outline: 0;
    cursor: text;
    -webkit-appearance: none;
    box-sizing: border-box;
    transition: border-color 0.2s ease-in-out;

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }

    @include edgtfPlaceholder {
        color: inherit;
    }
}

// mixins styles - end

