// animation mixin - start

@mixin keyframes($name) {
    @-webkit-keyframes #{$name} {
        @content;
    }

    @keyframes #{$name} {
        @content;
    }
}

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// heading mixins - start

@mixin edgtfDefaultHeadingStyle() {
    @include edgtfHeadingStyle();
    margin: 25px 0;

    -ms-word-wrap: break-word;
    word-wrap: break-word;
    
    a {
        @include edgtfTypographyLayout();
        
        &:hover {
            color: $first-main-color;
        }
    }
}

@mixin edgtfHeadingStyle($with-heading: null, $with-color: true) {
    
    @if ($with-color) {
        color: $default-heading-color;
    }
    
    @if ($with-heading == 'h1') {
        @include edgtfH1();
    } @else if ($with-heading == 'h2') {
        @include edgtfH2();
    } @else if ($with-heading == 'h3') {
        @include edgtfH3();
    } @else if ($with-heading == 'h4') {
        @include edgtfH4();
    } @else if ($with-heading == 'h5') {
        @include edgtfH5();
    } @else if ($with-heading == 'h6') {
        @include edgtfH6();
    }
}

@mixin edgtfH1() {
    font-size: 50px;
    line-height: 1.2em;
    font-family: $default-heading-font;
    font-weight: 700;
}

@mixin edgtfH2() {
    font-size: 40px;
    line-height: 1.25em;
    font-family: $default-heading-font;
    font-weight: 700;
}

@mixin edgtfH3() {
    font-size: 30px;
    line-height: 1.2em;
    font-family: $default-heading-font;
    font-weight: 700;
}

@mixin edgtfH4() {
    font-size: 23px;
    line-height: 1.17em;
    font-family: $default-heading-font;
    font-weight: 700;
}

@mixin edgtfH5() {
    font-size: 20px;
    line-height: 1.2em;
    font-family: $default-heading-font;
    font-weight: 700;
}

@mixin edgtfH6() {
    font-size: 15px;
    line-height: 1.46em;
    font-family: $default-heading-font;
    font-weight: 700;
}

@mixin edgtfH6Aux() {
    font-size: 15px;
    line-height: 1.46em;
    font-family: $additional-heading-font;
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 0.1em;
}

// heading mixins - end

